body{
  margin: auto;
}


.navigation{
  background: #1E9600;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FF0000, #FFF200, #1E9600);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FF0000, #FFF200, #1E9600); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.titulo-menu a{
  font-size: 40px;
  color: #ffffff;
  
  margin-left: 18px;
  
}

.links a{
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  
  margin-left: 25px;
  margin-right: 10px;
}

/*Tarjetas*/

.cards{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

}

.product{
  max-width: 405px;
  background: #f79d00;  /* fallback for old browsers */
background: -webkit-linear-gradient(to top, #64f38c, #f79d00);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to top, #64f38c, #f79d00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  padding-bottom: 15px;
  border-radius: 10px;
  
}

.cards li{
  list-style-type: none;
  flex: 0 1 30rem;
  margin: 10px;    
}

.product-image{
  width: 360px;
  height: 200px;
  margin-left: 20px;
  margin-top: 20px;
}



.product-name {
  font-size: 25px;
  color: seashell;
  margin-left: 20px;
  margin-bottom: 10px;
}

.price{
  display: flex;
  justify-content: space-between;
  
}

.num{
  
  font-size: 30px;
  color: seashell;
  margin-left: 20px;
  margin-top: auto;
}

.boton{
  max-width: 70px;
  height: 30px;
  text-align: center;
  margin-right: 25px;
  text-decoration: none;
  color: brown;
  font-size: 30px;
  background-color: white;
  border-radius: 5px;
  padding: 5px;
}

.bienvenida{
  margin-top: 50px;
  font-size: 30px;
  color: #1E9600;
  text-align: center;
}

.imagen-inicial{
  width: 400px;
  height: 400px;
}

h1, h2, h3, h4, h5, h6, a {
  margin:0; padding:0;
}
.login {
  margin:0 auto;
  max-width:500px;
}
.login-header {
  color:#fff;
  text-align:center;
  font-size:300%;
}
/* .login-header h1 {
   text-shadow: 0px 5px 15px #000; */



.login-form {
  border:.5px solid #fff;
  background:#66B238;
  border-radius:10px;
  box-shadow:0px 0px 10px #000;
  
}
.login-form h3 {
  text-align:left;
  margin-left:40px;
  color:#fff;
}
.login-form {
  box-sizing:border-box;
  padding-top:15px;
	padding-bottom:10%;
  margin:5% auto;
  text-align:center;
}
.login input[type="text"],
.login input[type="password"],
.login input[type="email"] {
  max-width:400px;
	width: 80%;
  line-height:3em;
  font-family: 'Ubuntu', sans-serif;
  margin:1em 2em;
  border-radius:5px;
  border:2px solid #f2f2f2;
  outline:none;
  padding-left:10px;
}
.login-button {
  text-decoration: none;
  padding-left: 20px;
  padding-right: 20px;
  height:30px;
  width:100px;
  background:#fff;
  border:1px solid #f2f2f2;
  border-radius:20px;
  font-weight: bold;
  color: slategrey;
  text-transform:uppercase;
  font-family: 'Ubuntu', sans-serif;
  cursor:pointer;
}



